* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1.5;
    background-color: #fff;
    color: #424242;
    padding: 16px;
    margin: 0;
    background: rgb(143, 216, 211);
}

a {
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
    height: 185px;
}

input {
    padding: 10px;
    margin-right: 20px;
    width: 450px;
}

button {
    cursor: pointer;
}

.search-form {
    position: fixed;
    display: flex;
    justify-content: center;
    width: 100%;
    top: 0;
    font-size: 30px;
    padding: 20px;
    background-color: #1c8783fb;
    border-radius: 5px;
}

.load-more {
    display: block;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    padding: 10px 20px;
    border: none;
    border-radius: 3px;
    background-color: #1c8783fb;
    color: #ffffff;
}

.load-more:hover,
.load-more:focus {
    background-color: rgba(98, 137, 135, 0.435);
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
}

.gallery__section {
    padding: 20px;
    width: 1200px;
    margin: 0 auto;
}

.gallery__item {
    margin-left: 30px;
    margin-top: 30px;
    flex-basis: calc((100% - 120px) / 4);
    transition-property: transform;
    transition-duration: 250ms;
}

.gallery__item:hover,
.gallery__item:focus {
    transform: scale(1.2);
}

.photo-card {
    display: flex;
    flex-direction: column;
    background-color: rgba(98, 137, 135, 0.115);
    border-radius: 5px;
    box-shadow: 1px 1px 20px 1px rgb(26, 90, 80);
}

.info {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 55px;
    padding: 10px;
}

.info-item {
    display: flex;
    text-align: center;
    flex-direction: column;
    font-size: 10px;
    color: rgb(30, 81, 78);
}